import React, { Fragment, useEffect, useState } from 'react'
import axios from 'axios'
import Loading from '../../global/Loading';

const SubBooksComponent = () => {

    // const resourcesArr = [{
    //     link: '#',
    //     title: ' -:',
    //     descr: [{ text: 'InternetUrok.ru', link: '#' }, { text: '  ', link: '#' }, { text: ' ', link: '#' }, { text: '    ', link: '#' }, { text: '-   ', link: '#' }, { text: '     ', link: '#' }, { text: ' ', link: '#' }, { text: '  - ', link: '#' }]
    // }, {
    //     link: '#',
    //     title: ' :',
    //     descr: [{ text: '    ', link: '#' }, { text: '   ', link: '#' }, { text: '  . .. ', link: '#' }, { text: '  ', link: '#' }, { text: '  ', link: '#' }]
    // }, {
    //     link: '#',
    //     title: ' :',
    //     descr: [{ text: ' ', link: '#' }, { text: '  /  .   ', link: '#' }, { text: '', link: '#' }, { text: ' ', link: '#' }, { text: '  ', link: '#' }, { text: '', link: '#' }, { text: ' ', link: '#' }, { text: '   ', link: '#' }, { text: ' ', link: '#' }]
    // }, {
    //     link: '#',
    //     title: ' :',
    //     descr: [{ text: '', link: '#' }, { text: '', link: '#' }, { text: '    ', link: '#' }, { text: 'Megabook', link: '#' }, { text: ' ', link: '#' }, { text: '  ', link: '#' }, { text: '    ', link: '#' }]
    // }]

    const [ResourcesArr, setResourcesArr] = useState([]);
    const [IsLoadded, setIsLoadded] = useState(false)

    useEffect(() => {
        axios.get('http://localhost:8000/wp-json/wp/v2/resources__book-page')
            .then(res => {
                const arr = res.data
                setResourcesArr(arr)
                setIsLoadded(true)
                console.log(ResourcesArr)
            })
            .catch(err => console.log(err))
    }, [setResourcesArr])

    if (IsLoadded === true) {
        return (
            <Fragment>
                { ResourcesArr.map((ResourcesArr) => <div className="resources-block">{ResourcesArr.title.rendered}<span className="resources-block-linkText" dangerouslySetInnerHTML={{ __html: ResourcesArr.content.rendered }} /></div>)}
            </Fragment>
        )
    }
    return (
        <Loading />
    )
}
export default SubBooksComponent